import React from 'react'


export const ChangePasswordForm = (props) =>{
    return(
    <div className="container">
        <section>
            <form onSubmit={props.submitHandler} className="form-signin">
                <h2 className="form-signin-heading">Slaptazodzio keitimas</h2>
                <input className="form-control" type="password" name="oldPassword" onChange={props.fieldHandler} placeholder="Senas slaptaodis" required />
                <input className="form-control" type="password" name="newPassword" onChange={props.fieldHandler} placeholder="Slaptaodis" required/>
                <input className="form-control" type="password" name="newPasswordRepeat" onChange={props.fieldHandler} placeholder="Slaptaodis" required/>
                <div className="checkbox">
                </div>
                <button className="btn btn-lg btn-primary btn-block" type="submit">Prisijungti</button>
            </form>
        </section>
    </div> 
    )
}




